import Vue from 'vue'
import Vuex from 'vuex'
import user from './user'
import login from './login'
import project from './project'
Vue.use(Vuex)

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production', //
  modules: {
    user,
    login,
    project
  }
})



